import React from 'react';
import styled from 'styled-components';
import img from '../assets/.jpg';
const Nav = () => {
  return <NavContainer>     API</NavContainer>;
};

const NavContainer = styled.div`
  width: 100%;
  height: 300px;
  line-height: 300px;
  display: block;
  text-align: center;
  font-weight: bold;
  font-size: 2rem;
  background-image: url(${img});
`;
export default Nav;
